@import "variables";

header {
  height: 70px;
  position: sticky;
  top: 0;
  z-index: 2;
  border-bottom: 1px solid transparent;
  transition: 0.2s;

  //.light-theme & {
  //  background-color: $wht;
  //  border-color: #00000020;
  //
  //  .right-side {
  //    background-color: $wht;
  //    border-color: #00000020;
  //  }
  //}

  //.dark-theme & {
  background-color: $blk;
  color: $wht;
  border-color: #ffffff20;

  .right-side {
    background-color: $blk;
    border-color: #ffffff20;
  }

  //}

  > .working-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .left-side, .right-side {
    display: flex;
    align-items: center;
    flex: 1;
  }

  .right-side {
    justify-content: flex-end;
    height: 100%;
  }

  .left-side {
    z-index: 1;
    justify-content: space-evenly;
  }

  .logo {
    width: 50px;
    height: 50px;
    margin-right: 5px;

    display: flex;
    justify-content: center;
    align-items: center;

    > svg {
      height: 100%;
      width: auto;
    }
  }

  .name {
    font-size: clamp(1em, 6vw, 1.5em);
    font-weight: 900;
    text-transform: uppercase;
    overflow: hidden;
    width: 90%;
    white-space: nowrap;
    transition: 0.2s;
  }
}

header button {
  background: none;
  font-size: 0.9em;
  font-weight: 300;
  text-transform: none;
  margin: 0 5px;
  height: 70px;
  box-shadow: 0 2px 0 0 var(--borderColor);
  border-radius: 0;
  //width: 120px;
  flex: 1;
  box-sizing: border-box;

  //.light-theme & {
  //color: $blk;
  //}

  //.dark-theme & {
  color: inherit;
  //}

  &:hover {
    box-shadow: 0 4px 0 0 var(--borderColor);
    font-size: 0.95em;
    @extend .theme-bkg-inv-light
  }

  &:focus {
    transform: scale(1);
  }
}
