@import "variables";

.section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section-title {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2em;
  margin: 70px 0 30px;
  -webkit-text-stroke: 1px transparent;
  z-index: 1;
  width: 100%;

  &.colored {
    h1, .section-number, .hex-stroke {
      background-position: 0 0;
    }
  }

  h1 {
    letter-spacing: -2px;
    font-weight: 700;
    line-height: 1.2;
    margin: 0;
    font-size: 1.2em;
    padding: 0 5px;
    background-size: 100% 200%;
    transition: 0.5s;
    background-position: 0 100%;

    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    .light-theme & {
      background-image: linear-gradient(0deg, $wht 50%, transparent 50%), linear-gradient(90deg, var(--startColor) 0%, var(--endColor) 100%);
    }

    .dark-theme & {
      background-image: linear-gradient(0deg, $blk 50%, transparent 50%), linear-gradient(90deg, var(--startColor) 0%, var(--endColor) 100%);
    }

    @extend .theme-stroke-inv;
  }
}

.section .section-contents {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

