// 3
.section.education {

  .section-contents {
    flex-direction: row;
    align-items: flex-start;
    flex-wrap: wrap;
  }

  .glass-card {
    //max-width: 550px;
    margin: 0 0 20px;

    &:last-child {
      margin: 0;
    }
  }
}

// 4
.section.games {
  .glass-card {
    flex-direction: row;
    margin: 0 0 40px;
    align-items: center;

    .content {
      flex: 1;
      padding: 10px 20px 0;
      text-align: justify;
      text-align-last: center;
    }

    .game-image {
      flex: 1;
      max-width: 500px;
      border-radius: 20px;
      overflow: hidden;
      //height: 100%;
      margin: 0 10px;
      display: flex;
      align-items: center;
      justify-content: center;

      > img {
        width: 100%;
        height: auto;
      }

    }
  }
}
