@import "variables";

.section.work > .section-contents {
  width: 100vw;
  height: 600px;
  background-color: #00000040;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;

  > .border-element-container {
    position: absolute;
    height: 0;
    width: 100%;
    z-index: 1;

    > .border-element {
      height: 30px;
      pointer-events: none;
    }

    &.top-border {
      top: 0;

      > .border-element {
        background: linear-gradient(180deg, #00000040, #00000000);
      }
    }

    &.bottom-border {
      bottom: 30px;

      > .border-element {
        background: linear-gradient(0deg, #00000050, #00000000);
      }
    }
  }
}

.section.work > .section-contents > .working-content {
  display: flex;
  flex-direction: column;
  position: relative;
}

.section.work > .section-contents .glass-card {
  height: 470px;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.work-card {
  width: calc(100% - 48px);
  cursor: pointer;
  transition: 0.2s;
  position: absolute;

  @for $i from 1 through 8 {
    &:nth-child(#{$i}) {
      $baseCardTop : 50px + ($i - 1) * 85px;
      top: $baseCardTop;

      &:hover {
        top: $baseCardTop - 20px;
        &.hidden {
          top: 90%;
        }
      }
    }
  }

  &.shown {
    top: 40px !important;
  }

  &.hidden {
    top: 90%;
  }

  .glass-card {
    margin: 0 auto;
  }

  .project-name {
    position: absolute;
    top: 15px;
    left: -5px;
    background-color: $shadow;
    //color: $wht;
    border: 1px solid transparent;
    @extend .theme-border-inv;

    padding: 10px 15px 10px 20px;
    border-radius: 5px;
    z-index: 2;
  }
}
