@import "variables";

.hexagon {
  fill: $prp-dark;
  position: absolute;
  transform: translate(-50%, -50%);

  > svg {
    height: 100%;
    width: auto;
  }
}

.hex-icon {
  margin-right: 10px;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  > svg:first-child {
    color: $prp-dark;
    position: absolute;
    width: 60%;
    height: 60%;
  }

  > svg:nth-child(2) {
    fill: $wht;
    width: inherit;
    height: inherit;
  }

  header button & {
    display: none;

    > svg:first-child {
      color: $blk;
    }
  }
}

.hex-number {
  margin-right: 10px;
  width: 50px;
  height: 50px;
  font-weight: 900;

  mask: url('../assets/blank_hex.svg');
  mask-size: 100% 100%;
  mask-repeat: no-repeat;
  mask-position: 50% 50%;

  display: flex;
  align-items: center;
  justify-content: center;

  .hex-stroke {
    transition: 0.5s;
    display: flex;
    align-items: center;
    justify-content: center;

    width: 48px;
    height: 48px;

    mask: url('../assets/blank_hex.svg');
    mask-size: 100% 100%;
    mask-repeat: no-repeat;
    mask-position: 50% 50%;

    background-size: 100% 200%;
    background-position: 0 100%;
  }

  .light-theme & {
    .hex-stroke {
      background-image: linear-gradient(0deg, $wht 50%, var(--startColor) 50%);
    }
  }

  .dark-theme & {
    .hex-stroke {
      background-image: linear-gradient(0deg, $blk 50%, var(--startColor) 50%);
    }
  }

  @extend .theme-stroke-inv;
  @extend .theme-bkg-inv;
  @extend .theme-color;
}

.parallax-hexagons {
  pointer-events: none;
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  opacity: 0.5;
}

.theme-button {
  position: fixed;
  bottom: 10px;
  right: 10px;
  cursor: pointer;
  z-index: 1;
  width: 70px;
  height: 70px;

  .hex-icon {
    width: inherit;
    height: inherit;
  }

  svg:first-child {
    @extend .theme-color;
    transition: 0.2s;
  }

  svg:nth-child(2) {
    @extend .theme-fill-inv;
    transition: 0.2s;
  }

  &:hover {
    svg:first-child {
      transform: scale(1.1);
    }
  }
}
