@import "variables";

@media screen and (max-width: 1100px) {
  .section.games {
    .glass-card {
      flex-direction: column;
    }
  }
}

@media screen and (max-width: 1000px) {
  a.padded {
    .text {
      width: 0;
    }

    .hex-icon {
      margin-right: 0;
    }

    &:only-child {
      .text {
        width: calc(100% - clamp(24px, 5vw, 36px) - 10px);
      }

      .hex-icon {
        margin-right: 10px;
      }
    }

  }

  header {
    .right-side {
      align-items: flex-end;
      transition: 0.2s;
      width: 100%;
      //width: 0;
      position: absolute;
      right: 0;
      border-bottom: 1px solid transparent;
      overflow: hidden;
      padding: 0 0 4px;
      height: 5px;
      transform: translateY(40px);
    }

    &:hover {
      .right-side {
        height: 70px;
        transform: translateY(73px);
      }
    }
  }

  .game-images {
    > :first-child {
      display: none !important;
    }
  }

  .glass-card {
    .project-info {
      position: absolute;
      z-index: 1;
      //display: none;
    }
  }
}

@media screen and (max-width: 650px) {
  .working-content {
    width: $header-width;
  }

  header {
    .left-side {
    }

    .logo {
      margin: 0;
    }

    .name {
      width: 0;
    }

    .right-side {
      .text {
        display: none;
      }

      .hex-icon {
        display: flex;
        margin: 0;
      }
    }
  }

  .section.education {
    .glass-card {
      text-align: center;
    }
  }

  .glass-card .buttons {
    flex-direction: column;

    .text {
      width: calc(100% - clamp(24px, 5vw, 36px) - 10px);
    }

    .hex-icon {
      margin-right: 10px;
    }
  }

  .theme-button {
    width: 60px;
    height: 60px;
  }
}

@media screen and (max-width: 400px) {
  body {
    font-size: 16px;
  }

  .working-content {
    padding: 0 10px;
    width: calc(100% - 20px);
  }

  .work-card {
    width: calc(100% - 20px);
  }

  .glass-card .buttons .text, a.padded:only-child .text {
    width: 0;
  }
}
