
$wht: #e5e5e5;
//$blk: #1d1d1d;
$blk: #353535;
//$prp-dark : #593acb;
$prp-dark : #7100d3;
//$prp-light : #6d54c8;
$prp-light : #8454c8;
$wht-a: #e5e5e520;
$blk-a: #1d1d1d20;
$shine: #e5e5e540;
$shadow: #1d1d1d40;
$header-width: clamp(200px, calc(100vw - 100px), 1000px);
$body-width: clamp(160px, calc(80vw - 100px), 800px);


.theme-fill-inv {
  .light-theme & {
    fill: $blk;
  }

  .dark-theme & {
    fill: $wht;
  }
}

.theme-fill {
  .light-theme & {
    fill: $wht;
  }

  .dark-theme & {
    fill: $blk;
  }
}

.theme-color {
  .light-theme & {
    color: $wht;
  }

  .dark-theme & {
    color: $blk;
  }
}

.theme-color-inv {
  .light-theme & {
    color: $blk;
  }

  .dark-theme & {
    color: $wht;
  }
}

.theme-bkg {
  .light-theme & {
    background-color: $wht;
  }

  .dark-theme & {
    background-color: $blk;
  }
}

.theme-bkg-inv {
  .light-theme & {
    background-color: $blk;
  }

  .dark-theme & {
    background-color: $wht;
  }
}

.theme-bkg-inv-light {
  //.light-theme & {
  //  background-color: $blk-a;
  //}
  //
  //.dark-theme & {
    background-color: $wht-a;
  //}
}

.theme-border-inv {
  .light-theme & {
    border-color: $blk;
  }

  .dark-theme & {
    border-color: $wht;
  }
}

.theme-border-inv {
  .light-theme & {
    border-color: $blk;
  }

  .dark-theme & {
    border-color: $wht;
  }
}


.theme-stroke-inv {
  .light-theme & {
    -webkit-text-stroke-color: $blk;
  }

  .dark-theme & {
    -webkit-text-stroke-color: $wht;
  }
}

.theme-stroke-inv {
  .light-theme & {
    -webkit-text-stroke-color: $blk;
  }

  .dark-theme & {
    -webkit-text-stroke-color: $wht;
  }
}
