@import "variables";

.glass-card {
  height: 100%;
  width: 100%;
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(5.0px);
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  padding: 20px;
  box-sizing: border-box;
  overflow: hidden;
  box-shadow: 0 8px 10px 0 $shadow;
  display: flex;
  flex-direction: column;
  white-space: pre-wrap;

  > div {
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .light-font {
    font-weight: 300;
    font-style: italic;
  }

  .big-font {
    font-weight: 700;
    margin-right: 10px;
  }

  .small-font {
    font-weight: 300;
    font-size: 0.75em;
  }
}

.glass-card .buttons {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;

  a {
    margin: 10px;
    width: 100%;
    //flex: 1;
    max-width: 300px;
  }
}

.glass-card .project-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  flex: 1;

  .project-logo {
    font-size: 1.25em;
    font-weight: 700;
  }

  .project-description {
    margin: 10px auto 0;
    text-align: center;
    transition: 0.2s;
  }
}

.glass-card .project-images {
  transform: translateX(20%);
  max-width: 50%;
  flex: 1;
  display: flex;

  .img1 {
    position: relative;
    z-index: 2;
    transform: translateX(30%) translateY(5%);

    &:only-child {
      transform: translateX(-10%);
    }
  }

  .img2 {
    position: relative;
    z-index: 0;
    transform: translateY(-5%);
  }
}

