@import 'variables';

.start-up-anim {
  width: 100%;
  height: 100vh;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  z-index: 10;
  white-space: nowrap;
  transition: 0.2s;

  @extend .theme-bkg;

  &.hide {
    opacity: 0;
    pointer-events: none;
  }

  > .layer {
    position: absolute;
    width: 100vw;
    height: 100vh;
  }

  .full-logo {
    width: var(--width);
    height: var(--width);
    display: flex;
    justify-content: center;
    align-items: center;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    > svg {
      position: absolute;
      height: 100%;
      width: auto;
    }
  }

  .names {
    position: absolute;
    text-align: center;
    line-height: 0.7;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(220px);
    font-size: 3em;
    width: fit-content;

    > .heavy {
      font-weight: 900;
      text-transform: uppercase;
    }

    > .light {
      font-weight: 300;
      font-size: 0.5em;
    }
  }
}

.start-up-anim > .type-3 {
  .full-logo {
    animation-name: logo-shing;
    transform: translate(-50%, -50%) scale(1.01);
  }

  @keyframes logo-shing {
    0% {
      clip-path: polygon(50% 0%, 50% 0%, 50% 0%, 0% 25%, 0% 25%, 0% 25%);
    }
    100% {
      clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
    }
  }
}

.start-up-anim > .type-4 {
  .names {
    animation-fill-mode: forwards;
    animation-name: move-name-1;
  }

  @keyframes move-name-1 {
    0% {
      transform: translateX(-50%) translateY(0);
      font-size: 1em;
    }
    100% {
      transform: translateX(-50%) translateY(220px);
      font-size: clamp(1em, 10vw, 3em);
    }
  }
}

.start-up-anim > .type-5 {
  .names {
    font-size: clamp(1em, 10vw, 3em);
