@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700;900&display=swap');

@import "variables";

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
}

.theme-div {
  width: 100%;
  min-height: 100vh;
  box-sizing: border-box;
  transition: 0.5s background-color, 0.5s color;

  &.light-theme {
    background-color: $wht;
    color: $blk;
  }

  &.dark-theme {
    background-color: $blk;
    color: $wht;
  }
}

.working-content {
  padding: 0 24px;
  width: $body-width;
  margin: 0 auto;
  height: 100%;

  header & {
    width: $header-width;
  }
}

.page-body {
  max-height: 62vh;
  overflow: hidden;
  position: relative;
  padding-bottom: 50px;

  &.unclip {
    height: 100%;
    max-height: 100%;
  }
}

a, button {
  font-family: 'Montserrat', sans-serif !important;
  display: flex;
  align-items: center;
  justify-content: center;

  border-radius: 10px;
  background: linear-gradient(0deg, $prp-dark 50%, $prp-light 100%);
  background-size: 100% 200%;
  color: $wht;
  fill: $wht;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 1.1em;
  transition: 0.2s;
  box-sizing: border-box;
  cursor: pointer;
  border: none;
  outline: none;
  box-shadow: 0 8px 10px 0 $shadow;

  &:hover {
    background-position: 0 25%;
  }

  &:focus {
    transform: scale(0.95);
    background-position: 0 50%;
  }

  &.padded {
    padding: 15px;

    .text {
      overflow: hidden;
      transition: 0.2s;
      width: calc(100% - clamp(24px, 5vw, 36px) - 10px);
    }
  }
}

.bottom-btn {
  height: 70px;
  display: flex;
  justify-content: center;

  button {
    position: absolute;
  }
}
